@use 'foundation/variables'as v;
@use 'foundation/mixin'as *;

.l-footer {
  &__contents {
    color: #fff;
    background: #1B224C;
    padding-bottom: 6.5rem;
    @include mq('sp') {
      padding-bottom: 4rem;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;

    @include mq('sp') {
      display: block;
    }
  }

  /* nav
  ------------------------------*/
  &__nav-list {
    display: flex;
    align-items: center;
    margin-top: 4.55rem;
    font-size: 1.6rem;

    @include mq('sp') {
      display: block;
      padding-top: 2rem;
      margin-top: 0;
    }
  }

  &__nav-item {
    display: flex;
    align-items: center;
    @include mq('sp') {
      display: block;
      padding-top: 2rem;
      text-align: center;
    }
  }

  &__nav-link {
    padding-right: 2.1rem;
    @include mq('sp') {
      padding-right: 0;
    }
  }

  &__nav-item::after {
    content: "";
    display: inline-block;
    width: 1px;
    height: 2.3rem;
    background: #fff;
    margin-right: 2.5rem;

    @include mq('sp') {
      display: none;
    }
  }

  &__nav-item:last-child:after {
    display: none;
  }

  /* content
  ------------------------------*/
  &__info {
    text-align: right;
    margin-right: -1.8rem;

    @include mq('sp') {
      margin-right: 0;
      text-align: center;
    }
  }

  &__home-btn {
    width: 100%;
    max-width: 19.7rem;
    margin-top: 3.5rem;
    padding: 1.4rem 1.9rem 1.3rem;
    background: #fff;
    @include mq('sp') {
      margin-top: 4rem;
    }
  }

  &__home-btn img {
    width: 100%;
  }

  &__tel-num {
    font-size: 1.6rem;
    font-weight: bold;
    margin-top: 2.7rem;
    @include mq('sp') {
      margin-top: 2rem;
    }
  }

  &__tel-num span {
    display: inline-block;
    width: 1rem;
    margin-right: .7rem;
  }

  &__tel-num span img {
    width: 100%;
  }

  &__tel-date {
    font-size: 1.2rem;
    margin-top: 1.3rem;
    @include mq('sp') {
      margin-top: 1rem;
    }
  }

  /* copy write
  ------------------------------*/
  &__cw-text {
    margin-right: -1.8rem;
    padding: 1.2rem 0;
    font-size: 1.2em;
    text-align: right;

    @include mq('sp') {
      margin-right: 0;
    }
  }
}
