@use 'foundation/variables'as v;
@use 'foundation/mixin' as *;

.p-home {
  &__main-view {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 8.6rem;
    text-align: center;
    color: #fff;
    background: url('../img/fv.jpg') center center / cover;
    @include mq('sp') {
      height: calc(100vh - 6rem);
      justify-content: center;
    }
  }

  &__catch-copy {
    background: v.$navy;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  &__concept {
    padding-bottom: 3.9rem;
  }

  &__case {
    background: v.$navy;
    padding-bottom: 6rem;
  }

  &__process {
    padding-bottom: 7.5rem;
  }

  &__faq {
    padding-bottom: 7rem;
  }

  &__archives {
    display: flex;
    padding-bottom: 9rem;
    @include mq('sp') {
      display: block;
      padding-bottom: 0;
    }
  }
  &__blog {
    flex: 1;
  }
  &__news {
    flex: 1;
  }
}
